var allData = [
  ...vueData,
  ...jsData,
  ...develope,
  ...cssData,
  ...nodeData,
  ...reactData
];

allData = allData.sort((a, b) => {
  // ÎÇ†Ïßú Î¨∏ÏûêÏó¥ÏùÑ 'YYYY,MM.DD'ÏóêÏÑú 'YYYY-MM-DD' ÌòïÏãùÏúºÎ°ú Î≥ÄÌôò
  const dateA = new Date(a.date.replace(/,/g, '-'));
  const dateB = new Date(b.date.replace(/,/g, '-'));
  
  return dateB - dateA;  // ÏµúÏã† ÎÇ†ÏßúÏàúÏúºÎ°ú Ï†ïÎ†¨
});
export default allData;


